<template>
  <form @submit="onSubmit" class="add-form">
    <div class="form-control">
      <label>Prep</label>
      <input type="text" v-model="name" name="text" placeholder="Add Task" />
    </div>
    <div class="form-control">
      <label>Priority Level</label>
      <input
        type="text"
        v-model="day"
        name="day"
        placeholder="High/Low/Medium"
      />
    </div>
    <div class="form-control form-control-check">
      <label>Completed</label>
      <input type="checkbox" v-model="reminder"  name="reminder" />
    </div>

    <input type="submit" value="Save Prep" class="btn btn-block" />
  </form>
</template>

<script>
export default {
  name: 'AddTask',
  data() {
    return {
      name: '',
      day: '',
      reminder: false,
    }
  },
  methods: {
    onSubmit(e) {
      e.preventDefault()
      if (!this.name) {
        alert('Please add a task')
        return
      }
      const newTask = {
        
        name:this.name,
        day:this.day,
        reminder: this.reminder  
      }
      this.$emit('add-task',newTask)
      this.name = ''
      this.day = ''
      this.reminder = false
    },
  },
}
</script>

<style scoped>
.add-form {
  margin-bottom: 40px;
}
.form-control {
  margin: 20px 0;
}
.form-control label {
  display: block;
}
.form-control input {
  width: 100%;
  height: 40px;
  margin: 5px;
  padding: 3px 7px;
  font-size: 17px;
}
.form-control-check {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.form-control-check label {
  flex: 1;
}
.form-control-check input {
  flex: 2;
  height: 20px;
}
</style>